/* variables */
@use 'sass:color';
/* layout positioning */
$m-page-header: 8em !default;
$d-page-header: 15em !default;
$siteg: 1.2em !default; /* site gutter width */
$m-pagep: 1.568em !default; /* mobile horz page padding */
$d-pagep: 7.568em !default; /* desktop horz page padding */
$m-pagew: 100% !default; /* mobile page width */
$d-pagew: 1365px !default; /* desktop page width */
$pagepad: 2em !default; /* page vert padding */
$m-page-footer: 5em !default;
$d-page-footer: 5em !default;
$m-section-h2-height: 2em !default;
$d-section-h2-height: 2em !default;

/* brand colors */
/* Core brand tokens (raw palette) */
$brand-bg: #635d59 !default; /* background */
$brand-primary: #d5b35b !default; /* gold - primary brand */
$brand-accent: #3ea0d8 !default; /* blue - accent */
$brand-neutral: #a28d7a !default; /* taupe - neutral */

/* Back-compat aliases for existing code paths */
$color-1: $brand-bg !default; /* background */
$color-2: $brand-primary !default; /* primary */
$color-3: $brand-accent !default; /* secondary */
$color-4: $brand-neutral !default; /* alternative */

/* text */
/* Establish a base text color, then derive a small ramp from it. */
$text-base: #f0e5d4 !default; /* brighter base for text */
/* Tone-based text ramp (blend background INTO text; weight applies to first color) */
$text-bright: color.mix(
  $brand-bg,
  $text-base,
  0%
) !default; /* 0% bg, 100% text */
$text-high: color.mix(
  $brand-bg,
  $text-base,
  15%
) !default; /* 15% bg, 85% text */
$text-muted: color.mix(
  $brand-bg,
  $text-base,
  25%
) !default; /* 25% bg, 75% text */
$text-subtle: color.mix(
  $brand-bg,
  $text-base,
  35%
) !default; /* 35% bg, 65% text */
/* Optional alpha variants when blending is desired */
$text-muted-alpha: rgba($text-base, 0.75) !default;
$text-subtle-alpha: rgba($text-base, 0.6) !default;
/* Choose the sitewide default text role. You can re-point this to any variant. */
$color-text: $text-muted !default; /* set to any variant (e.g., $text-muted) */

/* Minimal tonal helpers */
/* Note: weight is percentage of the second color in color.mix().
   Define helpers so $p means "add $p of white/black to $c". */
@function tint($c, $p) {
  @return color.mix($c, white, $p);
}
@function shade($c, $p) {
  @return color.mix($c, black, $p);
}

/* Small ramps per brand */
$primary-light: tint($brand-primary, 18%) !default;
$primary-dark: shade($brand-primary, 18%) !default;

$accent-light: tint($brand-accent, 18%) !default;
$accent-dark: shade($brand-accent, 18%) !default;

$neutral-light: tint($brand-neutral, 14%) !default;
$neutral-dark: shade($brand-neutral, 14%) !default;

/* Surfaces */
$surface-0: $brand-bg !default; /* page bg */
$surface-1: tint($brand-bg, 6%) !default; /* cards/blocks */
$surface-2: tint($brand-bg, 10%) !default; /* elevated */

/* Borders and rules */
$rule: rgba($brand-neutral, 0.5) !default; /* hr/underlines */
$border: rgba($brand-neutral, 0.35) !default; /* control borders */

/* Focus & selection */
$focus-ring: rgba($brand-accent, 0.85) !default;
$selection: rgba($brand-accent, 0.25) !default;

/* Link role tokens (defined after strokes) */
/* moved below to ensure $stroke-* is defined */
/* suporting colors */
$gray: #959595 !default; /* neutral gray */
$gray-dark: #6d6863 !default; /* darker gray */
$white: #ffffff !default; /* white */
$red: #fc6969 !default; /* red - warning */
$teal: rgb(5, 231, 231) !default; /* teal - info */
$orange: #e4af00 !default; /* orange - alert */
$violet: #c512e1 !default; /* violet - error */
$green: #b4dc7f !default; /* green - tips */
$yellow: #f0d27b !default; /* yellow - notes */
$black: #000000 !default; /* black */

/* map to existing heading roles */
$color-h1: $brand-primary !default;
$color-h2: $brand-accent !default;
$color-h3: $brand-neutral !default;

$font-heading: 'Archivo', sans-serif !default; /* heading font */
$font-heading-narrow:
  'Archivo Narrow', sans-serif !default; /* heading narrow font */
$font-body: 'Roboto', sans-serif !default; /* body font */

/* fonts */
/* body */
$font-weight-body: 300 !default; /* Inter Regular becomes Normal */
$font-weight-body-light: 200 !default; /* Inter ExtraLight */
$font-weight-body-medium: 400 !default; /* Inter Medium becomes Medium */
$font-size-body: 1.7em !default;
$line-height-body: 1.35em !default;

/* headings */
$font-size-small: $font-size-body * 0.9 !default;
/* h1 */
$font-weight-h1: 800 !default; /* Poppins bold for headings */
$font-size-h1: $font-size-body * 2.25 !default;
$line-height-h1: 1.1em !default;
$color-h1: $color-2 !default;
/* h2 */
$font-weight-h2: 500 !default; /* lighter than regular for better balance */
$font-size-h2: $font-size-body * 1.9 !default;
$line-height-h2: 1.15em !default;
$color-h2: $brand-accent !default; /* h2 accent */
/* h3 */
$font-weight-h3: 700 !default;
$font-size-h3: $font-size-body * 1.2 !default;
$line-height-h3: 1.15em !default;
$color-h3: $brand-neutral !default;
/* h4 */
$font-weight-h4: 500 !default;
$font-size-h4: $font-size-body * 1.1 !default;
$line-height-h4: 1.15em !default;
$color-h4: $color-1;
/* h5 */
$font-weight-h5: 100 !default;
$font-size-h5: $font-size-body * 1.05 !default;
$line-height-h5: 1.15em !default;
$color-h5: $color-1;
/* h6 */
$font-weight-h6: 100 !default;
$font-size-h6: $font-size-body * 0.95 !default;
$line-height-h6: 1.15em !default;
$color-h6: $color-1;

/* stroke */
$stroke-1: 0.025em !default;
$stroke-2: 0.075em !default;
$stroke-3: 0.01em !default;

/* border */
$border-light: 0.05em solid #ccc !default;
$border-dark: 0.1em solid #333 !default;

/* body and page */
$site-bgcolor: #4f4a47 !default; /* site body background color */
$page-bgcolor: #635d59 !default; /* site page content background color */
$body-align: center !default; /* align all content in the site body */
$page-align: left !default; /* align the page content */

/* sections */
$section-bgcolor: $color-2 !default;

/* blocks */
$block-bgcolor: transparent !default;
$block-pad-top: 2em !default;
$block-pad-right: 2em !default;
$block-pad-bottom: 2em !default;
$block-pad-left: 2em !default;
$block-border-radius: $siteg * 0.25 !default;
$block-pad: $block-pad-top $block-pad-right $block-pad-bottom $block-pad-left !default;

/* nav variables */
$nav-align: center !default;
$link-pad-top: 1em !default;
$link-pad-right: 1em !default;
$link-pad-bottom: 1em !default;
$link-pad-left: 1em !default;
$link-pad: $link-pad-top $link-pad-right $link-pad-bottom $link-pad-left !default;
$m-link-size: 1.05em !default;
$d-link-size: 1.05em !default;
$link-height: 3em !default;
$link-space: 0.25em !default;
$d-link-space: 0.25em !default;
$link-radius: 0.25em !default;

/* default menu links */

/* menu links - link color */
$link-color: $color-2 !default;
$link-hover-color: $white !default;
$link-click-color: $color-1 !default;
$link-border: $stroke-1 solid rgba($color-1, 0.3) !default;

/* menu links - background color */
$link-bgcolor: rgba(transparent, 0) !default;
$link-hover-bgcolor: rgba($site-bgcolor, 0.5) !default;
$link-click-bgcolor: rgba($color-1, 0.3) !default;

/* menu links - border weight, stroke and color */
$link-border: $stroke-1 solid rgba($color-3, 0.5) !default;
$link-hover-border: $stroke-1 solid rgba($color-1, 0.75) !default;
$link-click-border: $stroke-1 solid rgba($color-1, 1) !default;

/* active menu links */

/* active menu links - active item link color */
$link-isactive-color: $white !default;
$link-isactive-hover-color: $white !default;
$link-isactive-click-color: $color-1 !default;

/* active menu links - active item background color */
$link-isactive-bgcolor: $color-2 !default;
$link-isactive-hover-bgcolor: $color-2 !default;
$link-isactive-click-bgcolor: rgba($color-1, 0.2) !default;

/* active menu links - active item border color */
$link-isactive-border: $stroke-1 solid rgba(transparent, 0) !default;
$link-isactive-hover-border: $stroke-1 solid rgba(transparent, 0) !default;
$link-isactive-click-border: $stroke-1 solid $white !default;

/* table variables */
$td-bg: green;

/******************/
/* form variables */
/******************/
$form-pad: 1em !default;
$form-border: $stroke-1 solid rgba($color-1, 0.5) !default;

/* admin table variables */
$form-td-pad: 1em !default;
$form-td-border: $stroke-2 solid rgba($color-1, 0.5) !default;
$form-td-bg: rgba(transparent, 0) !default;
$form-td-bg2: rgba($color-1, 0.05) !default;

/* wraps around form items */
$form-wrap-border: $stroke-1 solid rgba($color-1, 0.5) !default;
$form-wrap-pad: $siteg * 0.25 !default;
$form-wrap-bgcolor: rgba($color-1, 0.05) !default;
$form-summary-size: 1.1em !default;

/* form-items */
$form-item-pad: 1.25em !default;
$form-item-border: $stroke-1 solid rgba($color-1, 0.5) !default;
$form-item-after: 3em !default;
/* form item labels */
$form-label-size: 1.1em !default;

/* form text inputs */
$form-input-border: 1px solid rgba($site-bgcolor, 0.5) !default;
$form-input-pad: 0.75em 0.5em !default;
$form-input-color: $color-1;
$form-input-size: $font-size-body !default;
$form-input-bgcolor: $site-bgcolor !default;

/* form select fields */
$form-select-pad: 0.75em 1.5em 0.75em 0.75em;
$form-select-bgcolor: $form-input-bgcolor;
$form-select-border: $form-input-border;
$form-select-size: $form-input-size;

/* buttons */
$button-size: $font-size-body * 0.75 !default;
$button-height: 1.85em !default;
$button-pad-top: 0 !default;
$button-pad-right: 0.75em !default;
$button-pad-bottom: 0 !default;
$button-pad-left: 0.75em !default;
$button-pad: $button-pad-top $button-pad-right $button-pad-bottom
  $button-pad-left;
$button-radius: 0.25em !default;
/* button colors */
$button-color: $white !default;
$button-bgcolor: $color-2 !default;
$button-border: $stroke-1 solid rgba(transparent, 0) !default;
/* button - hover */
$button-hover-color: $white !default;
$button-hover-bgcolor: rgba($color-1, 0.5) !default;
$button-hover-border: $button-border !default;
/* button - click */
$button-click-color: $site-bgcolor !default;
$button-click-bgcolor: $color-1 !default;
$button-click-border: $stroke-1 solid rgba($color-2, 1) !default;
/* button - active */
$button-active-color: $color-2;
$button-active-bgcolor: $color-2 !default;
$button-active-border: $stroke-1 solid $color-2 !default;

/* https://base64.guru/converter/encode/image/svg */

$page-image-custom: url('data:image/svg+xml;base64,');

$page-image-config: url('data:image/svg+xml;base64,');

$page-image-article: url('data:image/svg+xml;base64,');

$default-image-1x1: url('data:image/svg+xml;base64,');

$default-profile-1x1: url('data:image/svg+xml;base64,');

$notice: url('data:image/svg+xml;base64,');
