.ck .ck.ck-editor__editable_inline {
  padding: 1.5em 0 !important;
  background-color: #635d59 !important;
}
.ck .ck.ck-editor__editable_inline > :first-child {
  margin-top: 0;
}

.ck.ck-content {
  /* removed debug border */
  margin: 0;
  /* load all body-styles from include file */
  /* these styles are imported into _body-style.scss and editor-styles.scss */
  /* include all styles that can apply to the editor body and the editor    */
  /* below, to style only the editor, use _editor-styles.scss after         */
  /* @import "custom-overwrites/globals/_body-style-include"                */
  /* position all block level element spacing from the bottom */
  /* h1 is outside the page-body in block layout */
  /* body-style headings are also used in modular sections */
  /* difinitions are found in globals/_mixins partial      */
  /* block-level elements */
  /* images with captions are wrapped in figure tags */
  /* alignment for figures */
  /* force svg images and containers to fill display width */
  /* text alignment helpers allowed by the Article format */
  /* /bodyStyle */
  /* /ck_content */
}
.ck.ck-content > h2:first-child {
  /* when the first child of the ckeditor is an h2, remove top margin*/
}
.ck.ck-content h2,
.ck.ck-content h3,
.ck.ck-content h4,
.ck.ck-content h5,
.ck.ck-content h6,
.ck.ck-content p,
.ck.ck-content blockquote,
.ck.ck-content figure,
.ck.ck-content ul,
.ck.ck-content ol,
.ck.ck-content li {
  margin-top: 0;
  font-weight: normal;
}
.ck.ck-content h2 {
  font-size: 3.23em;
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 0.25em;
  letter-spacing: -0.02em;
  font-family: "Archivo", sans-serif;
  color: #3ea0d8;
  font-weight: 500;
}
.ck.ck-content h2:after {
  margin-top: 0.2em;
  border-bottom: 2px solid rgba(62, 160, 216, 0.85);
  display: flex; /* prevent border-bottom from overlapping floats */
  content: "";
}
.ck.ck-content h3 {
  font-size: 2.04em;
  letter-spacing: 0.03em;
  font-family: "Archivo Narrow", sans-serif;
  margin-top: 0;
  margin-bottom: 2em;
  color: #a28d7a;
  font-weight: 700;
  text-transform: uppercase;
}
.ck.ck-content h4 {
  font-size: 1.87em;
  letter-spacing: -0.03em;
  margin-top: 0;
  margin-bottom: 2em;
  font-weight: 500;
}
.ck.ck-content h5 {
  font-size: 1.785em;
  margin-top: 0;
  margin-bottom: 1em;
  color: #959595;
}
.ck.ck-content h6 {
  font-size: 1.615em;
  margin-top: 0;
  margin-bottom: 0.5em;
}
.ck.ck-content p,
.ck.ck-content blockquote,
.ck.ck-content figure,
.ck.ck-content ul,
.ck.ck-content ol {
  margin-bottom: 2em;
  font-family: "Roboto", sans-serif;
  line-height: 1.35em;
  font-size: 1.7em;
  /* max-width: 40rem; use max-width to improve line-length/readability sitewide */
}
.ck.ck-content ul,
.ck.ck-content ol {
  /* remove margin-bottom from nested lists */
}
.ck.ck-content ul ul,
.ck.ck-content ul ol,
.ck.ck-content ol ul,
.ck.ck-content ol ol {
  margin-bottom: 0;
}
.ck.ck-content ul,
.ck.ck-content ol {
  list-style-position: outside;
  margin-left: 0;
  padding-left: 1.5em;
  overflow: hidden; /* prevent lists from extending into floats */
}
.ck.ck-content ul li,
.ck.ck-content ol li {
  padding-left: 0.5em;
  margin-bottom: 2px;
}
.ck.ck-content ul li:last-child,
.ck.ck-content ol li:last-child {
  margin-bottom: 0;
}
.ck.ck-content ul ul {
  list-style-type: circle;
}
.ck.ck-content ul ul ul {
  list-style-type: square;
}
.ck.ck-content ul ul ul ul {
  list-style-type: circle;
}
.ck.ck-content li img,
.ck.ck-content p img {
  /* display inline images within text */
  vertical-align: middle;
  border: none;
  margin: 0 0.5em;
}
.ck.ck-content figure {
  margin: 0;
  margin-bottom: 1em;
  width: 100%;
}
@media all and (min-width: 768px) {
  .ck.ck-content figure figure[class^=display--main_66] {
    max-width: 65%;
  }
  .ck.ck-content figure figure[class^=display--main_50] {
    max-width: 49%;
  }
  .ck.ck-content figure figure[class^=display--main_33] {
    max-width: 32%;
  }
  .ck.ck-content figure figure[class^=display--main_25] {
    max-width: 24%;
  }
}
.ck.ck-content figure p {
  display: inline; /* remove spacing from paragraph tags in media items */
  margin-bottom: 0;
}
.ck.ck-content figure div {
  display: inline;
}
.ck.ck-content figure img {
  padding: 0;
  margin: 0; /* remove margin from img in figure tag */
  display: block;
}
@media all and (min-width: 768px) {
  .ck.ck-content figure img {
    border-bottom: 0;
  }
}
.ck.ck-content figure img.align-center img {
  margin: 0 auto; /* use margin to center img in figure tag */
}
.ck.ck-content figcaption {
  color: #959595;
  outline-offset: -1px;
  background-color: unset;
  margin: 0 !important;
  padding: 2em 0 0 0 !important;
  font-size: 0.75em;
  font-style: italic;
}
.ck.ck-content figure .media {
  /* when media is inside a figure, remove margins, don't align and fill width */
  float: none;
  width: 100%;
  max-width: 100%;
  margin: 0 !important;
}
.ck.ck-content figure .media img {
  display: block;
  width: 100%;
}
.ck.ck-content .align-right,
.ck.ck-content .align-left,
.ck.ck-content .align-center {
  float: none; /* do not float on mobile */
  margin-bottom: 1em;
}
.ck.ck-content .align-right p,
.ck.ck-content .align-left p,
.ck.ck-content .align-center p {
  /* render p as inline */
  display: inline;
}
@media all and (min-width: 768px) {
  .ck.ck-content .align-right {
    float: right;
    margin-left: 2em;
    max-width: 65%;
  }
  .ck.ck-content .align-right img.inline-img {
    width: 100% !important; /* make aligned image fit figure */
  }
}
@media all and (min-width: 768px) {
  .ck.ck-content .align-left {
    float: left;
    margin: 0 2em 0 0;
    max-width: 65%;
  }
  .ck.ck-content .align-left img.inline-img {
    width: 100% !important; /* make aligned image fit figure */
  }
}
.ck.ck-content .align-center {
  text-align: center;
  margin: 0 auto 1em auto;
}
@media all and (min-width: 768px) {
  .ck.ck-content .align-center {
    margin: 0 auto;
    max-width: 95%;
  }
}
@media all and (min-width: 768px) {
  .ck.ck-content figure:has([class*=" display--main_100"]) {
    max-width: 100%;
  }
  .ck.ck-content figure:has([class*=" display--main_66"]) {
    max-width: 65%;
  }
  .ck.ck-content figure:has([class*=" display--main_50"]) {
    max-width: 49%;
  }
  .ck.ck-content figure:has([class*=" display--main_33"]) {
    max-width: 32%;
  }
  .ck.ck-content figure:has([class*=" display--main_25"]) {
    max-width: 24%;
  }
}
.ck.ck-content span.highlight {
  background-color: yellow;
  display: inline-block;
}
@media all and (min-width: 768px) {
  .ck.ck-content [class*=" display--main_66"],
  .ck.ck-content [data-view-mode*=" main_66"] {
    max-width: 65%;
  }
  .ck.ck-content [class*=" display--main_50"],
  .ck.ck-content [data-view-mode*=" main_50"] {
    max-width: 49%;
  }
  .ck.ck-content [class*=" display--main_33"],
  .ck.ck-content [data-view-mode*=" main_33"] {
    max-width: 32%;
  }
  .ck.ck-content [class*=" display--main_25"],
  .ck.ck-content [data-view-mode*=" main_25"] {
    max-width: 24%;
  }
}
.ck.ck-content .media-ext--svg {
  width: 100%; /* on handheld svg media is 100% width */
}
.ck.ck-content .media-ext--svg img {
  width: 100%;
}
@media all and (min-width: 768px) {
  .ck.ck-content .media-ext--svg[class*=display--main_100] {
    width: 100%;
  }
  .ck.ck-content .media-ext--svg[class*=display--main_66] {
    width: 65%;
  }
  .ck.ck-content .media-ext--svg[class*=display--main_50] {
    width: 49%;
  }
  .ck.ck-content .media-ext--svg[class*=display--main_33] {
    width: 32%;
  }
  .ck.ck-content .media-ext--svg[class*=display--main_25] {
    width: 24%;
  }
  .ck.ck-content .media-ext--svg img {
    width: 100%;
  }
}
.ck.ck-content blockquote {
  margin: 0 1em 2em 0;
  padding: 2em 6em 2em 3em;
  border-left: 6px solid #ccc !important;
}
.ck.ck-content blockquote::before {
  display: none; /* hide gin border */
}
.ck.ck-content blockquote p {
  margin-top: 0;
  font-weight: 200;
  font-style: italic;
}
.ck.ck-content blockquote p:last-child {
  margin-bottom: 0;
}
.ck.ck-content blockquote p strong {
  font-weight: 400; /* 500 */
}
.ck.ck-content .ck-horizontal-line hr,
.ck.ck-content hr {
  height: 0px;
  border-top: 2px solid #959595;
  clear: both;
}
.ck.ck-content .text-align-left {
  text-align: left;
}
.ck.ck-content .text-align-center {
  text-align: center;
}
.ck.ck-content .text-align-right {
  text-align: right;
}
.ck.ck-content .text-align-justify {
  text-align: justify;
}
.ck.ck-content a.button--red {
  background-color: #d83933;
  color: #fff;
}
.ck.ck-content a.button--red:hover {
  background-color: #b50909;
}
.ck.ck-content a.button--red:active {
  background-color: #8b0a03;
}
.ck.ck-content a.button--orange {
  color: #1b1b1b;
  background-color: #fa9441;
}
.ck.ck-content a.button--orange:hover {
  color: #fff;
  background-color: #c05600;
}
.ck.ck-content a.button--orange:active {
  color: #fff;
  background-color: #775540;
}
.ck.ck-content table {
  border: 1px double #b3b3b3;
  border-collapse: collapse;
  border-spacing: 0;
  height: 100%;
  width: 100%;
  margin-bottom: 3em;
}
.ck.ck-content table th,
.ck.ck-content table td {
  border: 1px double #b3b3b3;
  padding: 0.4em;
  min-width: 2em;
}
.ck.ck-content table th {
  background-color: rgba(0, 0, 0, 0.05);
  font-weight: 700;
}
.ck.ck-content table td {
  border: 1px double #b3b3b3;
  padding: 0.25em;
}
.ck.ck-content .drupal-media-style-align-left,
.ck.ck-content .image-style-align-left {
  /* applies to to inline images */
  margin: 0 2em 1em 0;
  max-width: 65%;
}
.ck.ck-content .drupal-media-style-align-right,
.ck.ck-content .image-style-align-right {
  /* applies to to inline images */
  margin: 0 0 1em 2em;
  max-width: 65%;
}
.ck.ck-content .drupal-media-style-align-center,
.ck.ck-content .image-style-align-center {
  /* applies to to inline images */
  text-align: center;
  margin: 0 auto 1em auto;
  max-width: 95%;
}
.ck.ck-content li img,
.ck.ck-content p img {
  /* display inline images within text */
  vertical-align: middle;
  border: none;
  margin: 0 0.5em;
}
.ck.ck-content .media[class^=display--main_66],
.ck.ck-content figure[data-view-mode^=main_66] {
  width: 65%;
  max-width: 65%;
}
.ck.ck-content .media[class^=display--main_50],
.ck.ck-content figure[data-view-mode^=main_50] {
  width: 49%;
  max-width: 49%;
}
.ck.ck-content .media[class^=display--main_33],
.ck.ck-content figure[data-view-mode^=main_33] {
  max-width: 32%;
}
.ck.ck-content .media[class^=display--main_25],
.ck.ck-content figure[data-view-mode^=main_25] {
  max-width: 24%;
}

/* set background-color of editor in fullscreen */
.ck-editor[data-fullscreen=fullscreeneditor] {
  background-color: gray !important;
}

